<script>

  //导入验证表单模块
  import validForm from '../assets/js/validForm'

  export default {
    data() {
      return {
        checked: false,
        userInfo: {
          phone: '',
          password: ''
        },
        //false: 闭合，true：睁开
        isEye: false
      };
    },
    methods:{

      //跳转页面
      goPage(name) {
        this.$router.push({name});
      },

      //切换密码的格式
      viewPassword() {
        this.isEye = !this.isEye;
      },

      //登录
      login() {

        //进行验证表单
        if (validForm.validUserForm(this.userInfo)) {

          //复制对象
          let data = Object.assign({}, this.userInfo);

          //请求密钥
          data.appkey = this.appkey;

          //开启加载提示
          this.$toast.loading({
            message: '加载中...',
            forbidClick: true,

            //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
            duration:0
          });

          //发起注册请求
          this.axios({
            method: 'POST',
            url: '/login',

            //post传递参数需要写在data
            data
          }).then(result => {
            //关闭加载提示
            this.$toast.clear();
            
            if (result.data.code == 200) {
              //跳转到菜单
              this.$router.push({name: 'Menu'});

              //存储token，用于后面登录验证
              localStorage.setItem('__Tk', result.data.token);
              
            } else {
              this.$toast(result.data.msg);
            }
          }).catch(err => {
            //关闭加载提示
            this.$toast.clear();
            
          })
        }

      },
      forgot() {
        this.$router.push({name: 'Forgot'});
      }
    }
  }
</script>

